<template>
  <div class="pageContent">
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="时间：">
            <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="结算状态：">
            <el-select v-model="searchList.settleState" clearable>
              <el-option label="待结算" value="1"></el-option>
              <el-option label="已结算" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单编号：">
            <el-input style="width: 203px" v-model="searchList.businessNumber" clearable @input="change($event)" placeholder="订单编号"></el-input>
          </el-form-item>
          <el-form-item label="支付单号/流水号：">
            <el-input style="width: 203px" v-model="searchList.wxNumber" clearable @input="change($event)" placeholder="支付单号/流水号"></el-input>
          </el-form-item>
          <el-form-item label="支付模式：">
            <el-select v-model="searchList.payType" clearable>
              <el-option label="微信支付-自有" value="1"></el-option>
              <el-option label="微信支付-代销" value="0"></el-option>
              <el-option label="小程序" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-start" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-tabs v-model="searchList.tabType" @tab-click="getData">
        <el-tab-pane label="全部" name="11"></el-tab-pane>
        <el-tab-pane label="进行中" name="0"></el-tab-pane>
        <el-tab-pane label="待结算" name="1"></el-tab-pane>
        <el-tab-pane label="退款" name="10"></el-tab-pane>
        <el-tab-pane label="交易成功" name="2"></el-tab-pane>
        <el-tab-pane label="交易关闭（失败）" name="3"></el-tab-pane>
      </el-tabs>
      <el-table :data="list" tooltip-effect="dark" style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-table :data="props.row.details" tooltip-effect="dark" style="width: 100%" :show-header="false">
              <el-table-column width="200" prop="createTime"></el-table-column>
              <el-table-column width="400" prop="description"></el-table-column>
              <el-table-column width="200" prop="businessNumber"></el-table-column>
              <el-table-column width="200">
                <template slot-scope="scope">
                  <div>{{scope.row.wxNumber ? scope.row.wxNumber : '-'}}</div>
                </template>
              </el-table-column>
              <el-table-column label="对方名称" width="200" prop="toUser"></el-table-column>
              <el-table-column width="200">
                <template slot-scope="scope">
                  <div class="flex-row-center" v-if="scope.row.settleState == 0">
                    <div class="infoFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
                    <el-tag style="margin-left:4px;" type="danger">未入账</el-tag>
                  </div>
                  <div class="flex-row-center" v-else-if="scope.row.settleState == 1">
                    <div class="infoFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
                    <el-tag style="margin-left:4px;" type="danger">未入账</el-tag>
                  </div>
                  <div class="flex-row-center" v-else>
                    <div class="succeseFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
                    <i 
                      v-if="scope.row.tradeType != 3 && scope.row.tradeType != 25" 
                      @click="goToMybill(scope.row)"
                      style="font-size:20px;" 
                      class="warningFont el-icon-caret-bottom curP"
                    ></i>
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="单据状态" width="200" prop="settleStateName"></el-table-column>
              <el-table-column label="结算状态" width="200" prop="updateStatusName"></el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column label="时间" width="200" prop="createTime"></el-table-column>
        <el-table-column prop="description" label="名称" width="400">
          <template slot-scope="scope">
            <div class="flex-row-center">
              <div>{{scope.row.description}}</div>
              <el-tag v-if="scope.row.payType == 0" style="margin-left:4px;" type="warning">代销</el-tag>
              <el-tag v-if="scope.row.payType == 1 || scope.row.payType == 2" style="margin-left:4px;">自有</el-tag>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="订单编号" width="200" prop="businessNumber"></el-table-column>
        <el-table-column label="支付单号/流水号" width="200" prop="wxNumber"></el-table-column>
        <el-table-column label="对方名称" width="200" prop="toUser"></el-table-column>
        <el-table-column width="200">
          <template slot="header" slot-scope="scope">
            单据合计入账金额/明细
            <el-tooltip :data-type="scope.row" class="item" effect="dark" content="合计预计入账金额=订单入账部分 - 退款部分；包含待结算账单；只是预估入账金额，具体金额以实际入账为准；" placement="top">
              <i class="el-icon-question"></i>
            </el-tooltip>
          </template>
          <template slot-scope="scope">
            <div class="flex-row-center" v-if="scope.row.settleState == 0">
              <div class="infoFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
              <el-tag style="margin-left:4px;" type="danger">未入账</el-tag>
            </div>
            <div class="flex-row-center" v-else-if="scope.row.settleState == 1">
              <div class="infoFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
              <el-tag style="margin-left:4px;" type="danger">未入账</el-tag>
            </div>
            <div class="flex-row-center" v-else>
              <div class="succeseFont">￥{{scope.row.amount > 0 ? '+'+scope.row.amount : scope.row.amount}}</div>
              <i 
                v-if="scope.row.tradeType != 3 && scope.row.tradeType != 25" 
                @click="goToMybill(scope.row)"
                style="font-size:20px;" 
                class="warningFont el-icon-caret-bottom curP"
              ></i>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="单据状态" width="200" prop="settleStateName"></el-table-column>
        <el-table-column label="结算状态" width="200" prop="updateStatusName"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="goToInfo(scope.row)">详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopTradBill } from '@/api/assetsManage/settlementManage'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1,
        time:[],
        tabType:'11'
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      list:[],
      listTotal:0
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    getData(){
      this.searchList.startDate = this.searchList.time[0] || ''
      this.searchList.endDate = this.searchList.time[1] || ''
      this.searchList.tradeType = this.searchList.tabType == 11 ? '' : this.searchList.tabType
      getShopTradBill(this.searchList).then(res=>{
        console.log('数据列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){
      this.searchList.startDate = ''
      this.searchList.endDate = ''
      this.searchList.time = []
      this.searchList.settleState = ''
      this.searchList.businessNumber = ''
      this.searchList.wxNumber = ''
      this.searchList.payType = ''
      this.getData()
    },
    goToInfo(e){
      if(e.tradeType == 3){
        console.log('充值')
        this.$emit('changeActive','1');
        return;
      }
      if(e.tradeType == 25){
        console.log('提现')
        this.$router.push({
          path:'/assetsManage/account/cashoutDetail',
          query:{
            id:e.id
          }
        })
        return;
      }
      if(e.tradeType == 6){
        console.log('会员卡开通记录')
        this.$router.push({
          path:'/memberManage/memberCenter',
          query:{
            type:1
          }
        })
        return;
      }
      this.$router.push({
        path:'/order/orderList/orderinfo',
        query:{
          orderId:e.orderId
        }
      })
    },
    goToMybill(e){
      this.$router.push({
        path:'/assetsManage/myBill',
        query:{
          orderId:e.orderId,
          afterSaleId:e.afterSaleId
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .wordInfo{
    margin-left: 80px;
    .word{
      margin-right: 40px;
      font-size: 16px;
      font-weight: bold;
    }
  }
</style>